<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <div class="common-main box">

          <div class="vertical-container-sprz">
            <div class="vertical-timeline-block lcsp-done">
              <div class="vertical-timeline-icon" />
              <div class="vertical-timeline-content">
                <h4>开始</h4>
              </div>
            </div>

            <div v-for="(item,index) of lzrzXxList" :key="index" :class="getClass(item.status)">
              <div class="vertical-timeline-icon">
                <i class="iconfont iconduihao" />
              </div>
              <div class="vertical-timeline-content">
                <h4>
                  {{ item.taskName }}
                  <!-- <i class="iconfont iconarrow-up" /> -->
                </h4>
                <div class="vertical-timeline-hide">
                  <p>
                    <span class="sprz-name">{{ item.userName }}</span>
                    <span class="sprz-info">{{ item.taskCommentLabel }}</span>
                    <span class="sprz-stay">{{ item.stayTime }}</span>
                  </p>
                  <p>
                    <span class="sprz-date"><em>送达时间</em> {{ zhTime(item.createTime) }} </span>
                    <span class="sprz-date"><em>提交时间</em> {{ zhTime(item.submitTime) }}</span>
                  </p>
                  <div class="text-detail">
                    <span class="sprz-title">{{ item.taskCommentLabel }}：</span>{{ item.taskComment }}
                  </div>
                </div>
              </div>
            </div>
            <div class="vertical-timeline-block lcsp-undone">
              <div class="vertical-timeline-icon" />
              <div class="vertical-timeline-content">
                <h4>结束</h4>
              </div>
            </div>

          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
export default {
  components: {
    headerBar
  },
  data() {
    return {
      SpxzShow: false,
      headerData: {
        title: '流转日志',
        leftUrl: 'back',
        leftText: ' '
      },
      fhbh: '',
      lzrzXxList: []
    }
  },
  computed: {

  },
  created() {
    if (this.$store.state.fhgl.fhbh) {
      this.fhbh = this.$store.state.fhgl.fhbh
    }
    this.getLzrz()
  },
  methods: {
    getClass(status) {
      // status=1  lcsp-undone 未进行
      // status=2  lcsp-current 进行中
      // status=3  lcsp-done 已完成
      if (status === 3) {
        return 'vertical-timeline-block lcsp-done'
      } else if (status === 2) {
        return 'vertical-timeline-block lcsp-current'
      } else {
        return 'vertical-timeline-block lcsp-undone'
      }
    },
    zhTime(time) {
      if (time) {
        time = time + ''
        return time.slice(0, 4) + '-' + time.slice(4, 6) + '-' + time.slice(6, 8) + ' ' + time.slice(8, 10) + ':' + time.slice(10, 12) + ':' + time.slice(12, 14)
      }
    },
    getLzrz() {
      this.$store.dispatch('jbrdbsx/getlzrzlistActions', {
        id: this.fhbh
      }).then((res) => {
        this.lzrzXxList = res.data
      })
    }
  }

}
</script>

<style lang="css" scoped>

</style>
